import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	methods:{
		...mapMutations(['logout']),
		navTo(url){
			uni.navigateTo({
				url: url
			})
		},
		removeStorage() {
			uni.clearStorageSync()
			this.logout()
			this.$api.msg('已清除缓存！');
			uni.navigateBack()
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		this.logout();
						uni.removeStorageSync('haslogin')
						uni.navigateBack();
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
